$(function(){
  var $body = $('body'),
  ballActor = new ui.Actor({
    element: '#ball',
    values: {
      y: 0
    }
  });

  var showBall = new ui.Tween({
    values: {
      y: 300,
      opacity: 1
    }
  });

  var trackBall = new ui.Track({
    values: {
      x: {},
      y: {}
    }
  });

  var bounceUp = new ui.Simulate({
    values: {
      y: {
        velocity: -600,
        acceleration: 200
      }
    }
  });

  var bounceDown = new ui.Simulate({
    values: {
      y: {
        velocity: 600,
        acceleration: -200
      }
    }
  });

  ballActor
  .start(showBall.extend({
    duration: 500,
    ease: 'easeOut'
  }))
  .then(function () {
    $('#ball').on('mousedown touchstart', ballActor.element, function (event) {
      ballActor.start(bounceUp).then(bounceDown);
  });
});

});



